<template><div><h1 id="java-异常处理" tabindex="-1"><a class="header-anchor" href="#java-异常处理"><span>Java 异常处理</span></a></h1>
<h2 id="异常概述" tabindex="-1"><a class="header-anchor" href="#异常概述"><span>异常概述</span></a></h2>
<p>异常（Exception）是程序运行过程中出现的错误或异常情况。Java 中的异常分为：</p>
<ul>
<li>检查型异常（Checked Exceptions）</li>
<li>非检查型异常（Unchecked Exceptions）</li>
</ul>
